* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

.list {
  max-width: 1920px;
  width: calc(100% - 20px);
  margin: 20px auto;
  background-color: tomato;
  text-align: center;
  height: 100vh;
}

.list h2 {
  padding-top: 50px;
  font-size: 100px;
}

.list form {
  width: 700px;
  height: 500px;
  margin: 0 auto;
  color: #202020;
}

.list form input {
  margin-top: 50px;
  width: 500px;
  height: 50px;
}

.list form button {
  width: 500px;
  height: 30px;
  margin: 0 auto;
  display: block;
}

.list ul {
  
}

.list ul li {
  width: 600px;
  margin: 0 auto;
}